@keyframes showMessage {
  from {
    transform: translateY(1rem);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes hideMessage {
  from {
    transform: translateY(0);
    opacity: 1;
  }

  to {
    transform: translateY(1rem);
    opacity: 0;
  }
}



